import { useState } from "react";
import { Plus, ShoppingCart, FileText, Truck, CreditCard, Package, BarChart3, ArrowLeft } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { SalesTab } from "@/components/sales/SalesTab";
import { Sale } from "@/types/sales";
import TopMobileHeader from "@/includes/TopMobileHeader";
import BottomMobileNavigation from "@/includes/BottomMobileNavigation";
import { useNavigate, useLocation } from "react-router-dom";
import { SalesOverview } from "./SalesOverview";

export const SalePage = () => {
  const [sales, setSales] = useState<Sale[]>([]);
  const [activeTab, setActiveTab] = useState<'pos' | 'transactions' | 'purchase-orders' | 'deliveries' | 'summary'>('pos');
  const navigate = useNavigate();
  const location = useLocation();

  // Summary stats
  const totalSales = sales.reduce((sum, sale) => sum + sale.totalAmount, 0);
  const pendingSales = sales.filter(s => s.status === 'pending').length;
  const completedSales = sales.filter(s => s.status === 'paid').length;

  // Tab configuration based on SalesTab.tsx
  const salesTabs = [
    { id: 'pos' as const, label: 'POS', icon: CreditCard },
    { id: 'transactions' as const, label: 'Transactions', icon: FileText },
    { id: 'purchase-orders' as const, label: 'Purchase Orders', icon: Package },
    { id: 'deliveries' as const, label: 'Deliveries', icon: Truck },
    { id: 'summary' as const, label: 'Summary', icon: BarChart3 },
  ];

  const salesClient = {
    id: 'sales-entry',
    name: 'Sales Entry',
    contact_email: '',
    phone: '',
    notes: '',
    address: '',
    contact_person: '',
    department_count: 0,
    printer_count: 0,
    location_count: 0,
    created_at: '',
    updated_at: '',
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-purple-50">
      {/* Fixed container for Safari compatibility */}
      <div className="w-full min-h-screen bg-white shadow-xl" style={{ minWidth: '320px', maxWidth: '100vw' }}>
        <TopMobileHeader
          client={salesClient}
          onBack={() => navigate('/')}
          onEdit={() => {}}
          appVersion="1.0.0"
        />
        {/* Main Content - Fixed padding for Safari */}
        <div className="px-4 sm:px-6 lg:px-8 pb-24 overflow-y-auto" style={{ WebkitOverflowScrolling: 'touch' }}>
          <div className="mb-6">
            <h2 className="text-xl font-semibold text-gray-900">Sales Management</h2>
          </div>

          {/* Summary Cards */}
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
            <Card className="rounded-xl shadow-md">
              <CardContent className="p-4 text-center">
                <div className="text-2xl font-bold text-green-600">â‚±{totalSales.toFixed(2)}</div>
                <div className="text-sm text-gray-500 mt-1">Total Sales</div>
              </CardContent>
            </Card>
            <Card className="rounded-xl shadow-md">
              <CardContent className="p-4 text-center">
                <div className="text-2xl font-bold text-orange-600">{pendingSales}</div>
                <div className="text-sm text-gray-500 mt-1">Pending Sales</div>
              </CardContent>
            </Card>
            <Card className="rounded-xl shadow-md">
              <CardContent className="p-4 text-center">
                <div className="text-2xl font-bold text-blue-600">{completedSales}</div>
                <div className="text-sm text-gray-500 mt-1">Completed Sales</div>
              </CardContent>
            </Card>
          </div>

          {/* Sales Tab Content */}
          {activeTab === 'summary' ? (
            <SalesOverview />
          ) : (
            <SalesTab sales={sales} setSales={setSales} activeTab={activeTab} />
          )}
        </div>

        {/* Fixed Navigation Bar with back button enabled */}
        <BottomMobileNavigation
          activeTab={activeTab}
          onTabChange={(tabId) => setActiveTab(tabId as 'pos' | 'transactions' | 'purchase-orders' | 'deliveries' | 'summary')}
          tabs={salesTabs}
          showBackButton={true}
          onBackClick={() => navigate('/')}
        />
      </div>
    </div>
  );
};
